<template>
    <el-container class="topcontainer">
        <el-header><common-header /></el-header>
        <el-container class="midcontainer">
            <el-aside><common-aside /></el-aside>
            <el-main><router-view></router-view></el-main>
        </el-container>
        <el-footer><common-footer /></el-footer>
    </el-container>
</template>

<script>
import CommonHeader from '../components/CommonHeader.vue';
import CommonAside from '../components/CommonAside.vue';
import CommonFooter from '../components/CommonFooter.vue'
export default {
    data() {
        return {}
    },
    components: {
        CommonHeader,
        CommonAside
    }
}
</script>
<style scoped>
.topcontainer{
    height: 100%;
}
.midcontainer{
    position: relative;
    height: 100%;
    display: block;
    max-width: 1280px;
    margin-left: auto;
    margin-right: auto;
    margin-top:120px;
}
el-header {
    position: absolute;
    display: block;
    height: 80px;
    width: 100%;
    border-bottom: 1px solid #e6e6e6;
    display: block;


}

el-aside {
    position: fixed;
  
}
el-main{
    position: absolute;
    left: 385px;

}

</style>


