<template>
    <div>
        <h1>Working</h1>
        <template>
            <el-table :data="tableData" style="width: 100%; font-size:18px">
                <el-table-column prop="affiliation" label="Affiliation" width="450px">
                </el-table-column>
                <el-table-column prop="data" label="Start -- End Date" width="300px">
                </el-table-column>
                <el-table-column prop="position" label="Position" width="240px">
                </el-table-column>
            </el-table>
        </template>
    </div>
</template>
  
<script>
export default {
    data() {
        return {
            tableData: [
                {
                    affiliation: 'Southwest University',
                    data: '2021/03 – Now',
                    position: 'Professor'
                },
                {
                    affiliation: 'Singapore University of Technology and Design',
                    data: '2018/06 – 2021/04',
                    position: 'Research Fellow'
                },
                {
                    affiliation: 'University of Helsinki',
                    data: '2017/03 – 2018/05',
                    position: 'Research Fellow'
                },
                {
                    affiliation: 'TU Darmstadt',
                    data: '2017/10 – 2017/11',
                    position: 'Academic Visit'
                },
                {
                    affiliation: 'Chongqing University of Technology',
                    data: '2014/07 – 2020/10',
                    position: 'Associate Professor'
                },
            ]
        }
    }
}
</script>
<style>
h1{
    margin-bottom: 10px;
    margin-top: 10px;
    font-size: 1.563em;
}
</style>




