<template>
    <div class="bj">
        <el-menu :default-active="activeIndex" active-text-color=null class="el-menu-demo" mode="horizontal"
            @select="handleSelect">
            <el-menu-item @click="clickMenu(item)" v-for="item in menuData" :key="item.label" :index="item.label">
                <span slot="title" class="title">{{ item.label }}</span>
            </el-menu-item>
        </el-menu>
    </div>
</template>
<script>
export default {
    data() {
        return {
            activeIndex: '1',
            menuData: [

                {
                    path: "/yang",
                    name: "yang",
                    label: "Zheng Yang",
                    url: "Yang/Yang"
                },

                {
                    path: "/pub",
                    name: "pub",
                    label: "Publications",
                    url: "Publications/Publications"
                },
                {
                    path: "/working",
                    name: "working",
                    label: "Working",
                    url: "Working/Working"
                },
                {
                    path: "/services",
                    name: "services",
                    label: "Services",
                    url: "Services/Services"
                },
                {
                    path: "/cv",
                    name: "cv",
                    label: "CV",
                    url: "CV/CV"
                },

            ]
        };
    },
    methods: {
        handleSelect(key, keyPath) {
            console.log(key, keyPath);
        },
        //点击菜单
        clickMenu(item) {
            if (this.$route.path !== item.path && !(this.$route.path === '/yang' && (item.path == '/'))) {
                this.$router.push(item.path)
            }
        },
    }
}
</script>
<style scoped>
.bj {
    min-width: 1280px;
}

.el-menu-demo {
    height: 45px;
    padding: 18px;
    margin-left: 300px;
}

el-menu-item {
    margin-right: 16px;

}

.title {
    font-size: 18px;
    color: #7a8288;
    padding: 9px;
    margin: 18px;
    display: inline;
    position: relative;
}

.title::after {
    position: absolute;
    left: 0;
    top: 100%;
    content: '';
    background-color: #bdc1c4;
    height: 4px;
    width: 100%;
    transform: scale(0);
    -webkit-transform: scale(0);
    transition: all .2s;
    -webkit-transition: all .2s;
    /* transition:all 0.2s ease-in-out; */
}

.title:hover::after {
    height: 4px;
    -webkit-transform: scale(1);
    transform: scale(1);
    /* transition:all 0.2s ease-in-out; */
}
</style>


